.nav {
    background-color: #333;
    color: whitesmoke;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 0 1rem;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.nav a {
    /*color: inherit;*/
    color: #38BDDF;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
